body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.zoomPanel {
  background: #ffffff;
  position: absolute;
  color: #333333;
  z-index: 10;
  bottom: 5px;
  left: 5px;
  width: 70px;
  height: 10px;
  padding-bottom: 5px;
  text-align: center;
  font-size: 12px;
  border-color: black;
  border-style: solid;
  border-width: 1px;
}
